.rmp-block-menu-trigger {
    width: var(--rmp--menu-hamburger-width, 55px);
    height: var(--rmp--menu-hamburger-height, 55px);
    border-radius: var(--rmp--menu-hamburger-border-radius, 5px);
    text-decoration: none;
    background: var(--rmp--menu-hamburger-background, #1d4354);
    transition-duration: var( --rmp--menu-container-animation-transition-duration, 0.5s);
    display: block;
    border: none;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: space-evenly;
    color: var(--rmp--menu-hamburger-color, #ffffff);
    &.rmp-block-menu-trigger-position-right{
        margin-left: auto;
    }
    &.rmp-block-menu-trigger-position-left{
        margin-right: auto;
    }
    &.rmp-block-text-position-right {
        flex-direction: row;
    }
    &.rmp-block-text-position-left {
        flex-direction: row-reverse;
    }
    &.rmp-block-text-position-top {
        flex-direction: column-reverse;
    }
    &.rmp-block-text-position-bottom {
        flex-direction: column;
    }
    &.rmp-block-active{
        background: var(--rmp--menu-hamburger-active-background, #1d4354);
        color: var(--rmp--menu-hamburger-active-color, #ffffff);
        .rmp-block-trigger-box {
            .rmp-block-trigger-inner,
            .rmp-block-trigger-inner::before,
            .rmp-block-trigger-inner::after {
                background: var(--rmp--menu-hamburger-active-color, #ffffff);
            }
            svg{
                fill: var(--rmp--menu-hamburger-active-color, #ffffff);
            }
        }
        .rmp-block-trigger-label-inactive,
        .rmp-block-trigger-icon-inactive{
            display: none;
        }
        .rmp-block-trigger-label-active,
        .rmp-block-trigger-icon-active{
            display: block;
        }
        .rmp-block-trigger-box{
            .rmp-block-trigger-inner{
                transform: rotate(45deg);
                &:after{
                    bottom: 0;
                    transform: rotate(-90deg);
                }
                &:before{
                    opacity: 0;
                }
            }
        }
    }
    &:hover{
        background: var(--rmp--menu-hamburger-hover-background, #1d4354);
        color: var(--rmp--menu-hamburger-hover-color, #ffffff);
        .rmp-block-trigger-box {
            .rmp-block-trigger-inner,
            .rmp-block-trigger-inner::before,
            .rmp-block-trigger-inner::after {
                background: var(--rmp--menu-hamburger-hover-color, #ffffff);
            }
            svg{
                fill: var(--rmp--menu-hamburger-hover-color, #ffffff);
            }
        }
    }
    .rmp-block-trigger-box {
        width: var(--rmp--menu-hamburger-line-width, 25px);
        display: inline-block;
        position: relative;
        vertical-align: super;
        height: var( --rmp--menu-hamburger-trigger-box-height, 25px) ;
        display: flex;
        align-items: center;
        .rmp-block-trigger-inner {
            display: block;
        }

        .rmp-block-trigger-inner,
        .rmp-block-trigger-inner::before,
        .rmp-block-trigger-inner::after {
            width: var(--rmp--menu-hamburger-line-width, 25px);
            height: var(--rmp--menu-hamburger-line-height, 3px);
            background-color: var(--rmp--menu-hamburger-color, #ffffff);
            border-radius: 4px;
            position: absolute;
        }
        .rmp-block-trigger-inner,
        .rmp-block-trigger-inner::after {
            transition-duration: var( --rmp--menu-container-animation-transition-duration, 0.5s);
        }
        svg{
            fill: var(--rmp--menu-hamburger-color, #ffffff);
        }
        .rmp-block-trigger-icon{
            width: var(--rmp--menu-hamburger-icon-size, 30px);
            img{
                max-width: 100%;
            }
        }
        .rmp-block-trigger-inner::before,
        .rmp-block-trigger-inner::after {
            content: "";
            display: block;
        }

        .rmp-block-trigger-inner::before {
            top: var(--rmp--menu-hamburger-line-spacing, 10px);
        }

        .rmp-block-trigger-inner::after {
            bottom: var(--rmp--menu-hamburger-line-spacing, 10px);
        }
    }
    .rmp-block-trigger-label{
        font-family: var( --rmp--menu-hamburger-text-font, inherit);
        font-size: var( --rmp--menu-hamburger-text-size, 14px );
        line-height: var( --rmp--menu-hamburger-text-line-height, 14px );
    }
    .rmp-block-trigger-icon-active,
    .rmp-block-trigger-label-active{
        display: none;
    }
}

.wp-block-rmp-menu {
    .rmp-block-container {
        position: fixed;
        top: 0;
        margin: 0;
        transition: var(--rmp--menu-container-animation-transition-delay, 1s);
        overflow: auto;
        display: block;
        width: var(--rmp--menu-container-width, 100%);
        max-width: var(--rmp--menu-container-max-width, 350px);
        min-width: var(--rmp--menu-container-min-width, 320px);
        background-color: var(--rmp--menu-container-background-color, transparent);
        @if var( --rmp--menu-container-background-image ){
            background-image: var( --rmp--menu-container-background-image );
            background-size: var(--rmp--menu-container-background-size, cover);
            background-position: var(--rmp--menu-container-background-position, center);
            background-repeat: var(--rmp--menu-container-background-repeat, no-repeat);
        }
        height: 100%;
        left: 0;
        visibility: visible;
        z-index: 30;
        color: var(--rmp--menu-container-text-color, inherit);
        padding: var(--rmp--menu-container-padding, 0);

        .wp-block-image.aligncenter {
            margin-left: auto;
            margin-right: auto;
        }
        .wp-block-rmp-menu-items {
            list-style: none;
            transition: none;
            border-radius: 0;
            box-shadow: none;
            background: none;
            border: 0;
            bottom: auto;
            box-sizing: border-box;
            clip: auto;
            display: block;
            float: none;
            outline: none;
            overflow: visible;
            padding: 0;
            position: relative;
            pointer-events: auto;
            right: auto;
            text-align: left;
            text-decoration: none;
            text-indent: 0;
            text-transform: none;
            transform: none;
            top: auto;
            visibility: inherit;
            width: auto;
            word-wrap: break-word;
            white-space: normal;
            .wp-block-navigation-item{
                height: var(--rmp--menu-item-height, auto);
                line-height: var(--rmp--menu-item-line-height, 40px);
                padding: var(--rmp--menu-item-padding, 5px);
                font-size: var(--rmp--menu-item-font-size, 15px);
                font-weight: var(--rmp--menu-item-font-wieght, 300);
                font-family: var(--rmp--menu-item-font-family, inherit);
                text-align: var(--rmp--menu-item-text-align, left);
                letter-spacing: var(--rmp--menu-item-letter-spacing, 1px);
                text-transform: var(--rmp--menu-item-letter-case, none);
                text-decoration: none;
                color: var(--rmp--menu-item-color, #ffffff);
                background: var(--rmp--menu-item-background, transparent);
                @if "undefined" != var(--rmp--menu-item-border-top){
                    border-top: var(--rmp--menu-item-border-top);
                    border-right: var(--rmp--menu-item-border-right);
                    border-bottom: var(--rmp--menu-item-border-bottom);
                    border-left: var(--rmp--menu-item-border-left);
                }
                &:hover{
                    color: var(--rmp--menu-item-hover-color, #ffffff);
                    background: var(--rmp--menu-item-hover-background, transparent);
                    @if "undefined" != var(--rmp--menu-item-hover-border-top){
                        border-top: var(--rmp--menu-item-hover-border-top);
                        border-right: var(--rmp--menu-item-hover-border-right);
                        border-bottom: var(--rmp--menu-item-hover-border-bottom);
                        border-left: var(--rmp--menu-item-hover-border-left);
                    }
                    a{
                        color: var(--rmp--menu-item-hover-color, #ffffff);
                    }
                }
                a{
                    color: var(--rmp--menu-item-color, #ffffff);
                }
                &.current-menu-item{
                    color: var(--rmp--menu-item-active-color, #ffffff);
                    background: var(--rmp--menu-item-active-background, transparent);
                    @if "undefined" != var(--rmp--menu-item-active-border-top){
                        border-top: var(--rmp--menu-item-active-border-top);
                        border-right: var(--rmp--menu-item-active-border-right);
                        border-bottom: var(--rmp--menu-item-active-border-bottom);
                        border-left: var(--rmp--menu-item-active-border-left);
                    }
                    a{
                        color: var(--rmp--menu-item-active-color, #ffffff);
                    }
                    &:hover{
                        color: var(--rmp--menu-item-active-hover-color, #ffffff);
                        background: var(--rmp--menu-item-active-hover-background, transparent);
                        @if var(--rmp--menu-item-active-hover-border-top){
                            border-top: var(--rmp--menu-item-active-hover-border-top);
                            border-right: var(--rmp--menu-item-active-hover-border-right);
                            border-bottom: var(--rmp--menu-item-active-hover-border-bottom);
                            border-left: var(--rmp--menu-item-active-hover-border-left);
                        }
                        a{
                            color: var(--rmp--menu-item-active-hover-color, #ffffff);
                        }
                    }
                }
                .wp-block-navigation__submenu-container{
                    .wp-block-navigation-item{
                        line-height: var(--rmp--menu-subitem-line-height, auto);
                        padding: var(--rmp--menu-subitem-padding, 5px);
                        font-size: var(--rmp--menu-subitem-font-size, 15px);
                        font-weight: var(--rmp--menu-subitem-font-wieght, 300);
                        font-family: var(--rmp--menu-subitem-font-family, inherit);
                        text-align: var(--rmp--menu-subitem-text-align, left);
                        letter-spacing: var(--rmp--menu-subitem-letter-spacing, 1px);
                        text-transform: var(--rmp--menu-subitem-letter-case, none);
                        text-decoration: none;
                        color: var(--rmp--menu-subitem-color, #ffffff);
                        background: var(--rmp--menu-subitem-background, transparent);
                        @if "undefined" != var(--rmp--menu-subitem-border-top){
                            border-top: var(--rmp--menu-subitem-border-top);
                            border-right: var(--rmp--menu-subitem-border-right);
                            border-bottom: var(--rmp--menu-subitem-border-bottom);
                            border-left: var(--rmp--menu-subitem-border-left);
                        }
                        &:hover{
                            color: var(--rmp--menu-subitem-hover-color, #ffffff);
                            background: var(--rmp--menu-subitem-hover-background, transparent);
                            @if "undefined" != var(--rmp--menu-subitem-hover-border-top){
                                border-top: var(--rmp--menu-subitem-hover-border-top);
                                border-right: var(--rmp--menu-subitem-hover-border-right);
                                border-bottom: var(--rmp--menu-subitem-hover-border-bottom);
                                border-left: var(--rmp--menu-subitem-hover-border-left);
                            }
                            a{
                                color: var(--rmp--menu-subitem-hover-color, #ffffff);
                            }
                        }
                        a{
                            color: var(--rmp--menu-subitem-color, #ffffff);
                        }
                        &.current-menu-item{
                            color: var(--rmp--menu-subitem-active-color, #ffffff);
                            background: var(--rmp--menu-subitem-active-background, transparent);
                            @if "undefined" != var(--rmp--menu-subitem-active-border-top){
                                border-top: var(--rmp--menu-subitem-active-border-top);
                                border-right: var(--rmp--menu-subitem-active-border-right);
                                border-bottom: var(--rmp--menu-subitem-active-border-bottom);
                                border-left: var(--rmp--menu-subitem-active-border-left);
                            }
                            a{
                                color: var(--rmp--menu-subitem-active-color, #ffffff);
                            }
                            &:hover{
                                color: var(--rmp--menu-subitem-active-hover-color, #ffffff);
                                background: var(--rmp--menu-subitem-active-hover-background, transparent);
                                @if var(--rmp--menu-subitem-active-hover-border-top){
                                    border-top: var(--rmp--menu-subitem-active-hover-border-top);
                                    border-right: var(--rmp--menu-subitem-active-hover-border-right);
                                    border-bottom: var(--rmp--menu-subitem-active-hover-border-bottom);
                                    border-left: var(--rmp--menu-subitem-active-hover-border-left);
                                }
                                a{
                                    color: var(--rmp--menu-subitem-active-hover-color, #ffffff);
                                }
                            }
                        }
                    }
                }
            }
            .wp-block-navigation__submenu-container {
                display: none;
            }

            .has-child:hover .wp-block-navigation-submenu {
                display: block;
            }
        }
    }
}